.header {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 5rem;
    background-color: #fff;
    color: black;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10%;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 10;
  }
  .col {
    width: 45%
  }
  .col2 {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 55%;
  }
  .col3 {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    width: 55%;
  }
  
  .button {
    border-radius: 50px;
    background-color: #fff;
    color: black;
    padding: 0.7rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border-width: 0.5rem;
    font-weight: bold;
  }