.date {
    background-color: #fff;
    border-radius: 10px;
    display: flex; 
    flex-direction: column;
    text-align: center;
    box-shadow: 0 1px 2px rgba(0, 26, 255, 1);
    
}
.clickedDate {
    box-shadow: 0 1px 2px red;
}

.title {
    overflow-wrap: break-word;
    overflow-y: auto;
    height: 70px;
}

.buttons {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.button1 {
    background-color: #fff;
    border-color: rgba(0,0,0,0.3);
    color: black;
    padding: 0.2rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border-width: 0.1rem;
    font-weight: bold;
    border-radius: 0px 0px 0px 10px;
}

.button2 {
    background-color: #fff;
    border-color: rgba(0,0,0,0.3);
    color: black;
    padding: 0.2rem;
    padding-left: 1rem;
    padding-right: 1rem;
    border-width: 0.1rem;
    font-weight: bold;
    border-radius: 0px 0px 10px 0px;
}

.scheduledEvent {
    border-radius: 10px;
    background-color: aquamarine;
    height: 100px;
}