@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap');

* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #fff;
}

h3 {
  text-align: center;
}
h4 {
  text-align: center;
  margin-top: 10rem;
}
h5 {
  text-align: center;
}
p {
  text-align: center;
}

textarea {
  resize: none;
  background-color: #fff;
  border-color: black;
  border-width: 3px;
  border-radius: 5px;
  margin-top: 10px;
  width: 350px;
  height: 200px;
}

input {
  background-color: #fff;
  border-color: black;
  border-width: 3px;

}