.Dates {
    margin-left: 5rem;
    margin-right: 5rem;
    display: grid;
    grid-gap: 1rem;
    grid-template-columns: repeat(7, 150px);
    grid-template-rows: repeat(5, 150px);
    justify-content: center;
}
